// Button
.btn {
  border: 0.1em solid $dark-theme-light-grey;
  border-radius: 0.5em;
  padding: 0.5em;
  background: transparent;
  color: $dark-theme-light-grey;
  margin: 0.2em;

  &:hover {
    border-color: $green;
    box-shadow: 0 0 15px 0 $green;
    text-shadow: 0 0 15px 0 $green;
    color: $green;
    background: transparentize($green, 0.8); } }

// Content
#content {
  margin: 0 10em;
  font-size: 1.1em;

  h2 {
    font-size: 2em; } }

@media screen and (max-device-width: 700px) {
  #content {
    margin: 0 1.5em; } }

@media screen and (min-device-width: 700px) and (max-device-width: 1000px) {
  #content {
    margin: 0 5em; } }

// Imprint
#imprint-page {
  #imprint {
    margin-top: 5em;
    margin-left: 5em;
    margin-right: 5em;

    h1 {
      font-size: 4em;
      margin: 0.5em 0; } } }

@media screen and (max-device-width: 700px) {
  // Imprint
  #imprint-page {
    #imprint {
      margin-left: 2em;
      margin-right: 2em;

      h1 {
        font-size: 2em; } } } }

// Privacy Policy
#privacy-page {
  #privacy {
    margin-top: 5em;
    margin-left: 5em;
    margin-right: 5em;

    h1 {
      font-size: 4em;
      margin: 0.5em 0; } } }

// Responsive
@media screen and (max-device-width: 700px) {
  // Privacy Policy
  #privacy-page {
    #privacy {
      margin-left: 2em;
      margin-right: 2em;

      h1 {
        font-size: 2em; } } } }

// Error page
#error-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;

  h1 {
    font-size: 5em; } }
